	  .menu {
		padding: 10rem 0 20vh 0;
		width: 90vw;
		position: relative;
		display: flex;
		flex-direction: column;
		text-align: right;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		counter-reset: menucounter;
	}
	
	.menu__item {
		flex: none;
		display: flex;
		justify-content: flex-end;
		position: relative;
		padding-left: 5rem;
	}
	
	.menu__item::before {
		counter-increment: menucounter;
		content: counters(menucounter, ".", decimal-leading-zero);
		position: absolute;
		left: 0;
		border-left: 3px solid currentColor;
		top: 20%;
		height: 60%;
		padding-left: 1rem;
		display: flex;
		align-items: center;
		line-height: 1;
		font-weight: bold;
		opacity: 0;
		transform: translateX(-1rem);
		transition: transform 0.3s, opacity 0.3s;
	}
	
	.menu__item:hover::before {
		opacity: 1;
		transform: translateX(0);
	}
	
	.menu__item-text {
		position: relative;
		cursor: pointer;
		padding: 0.5rem;
		display: block;
		overflow: hidden;
		font-size: 6vw;
	}
	
	.menu__item-textinner {
		display: block;
		font-family: goldenbook, serif;
		font-weight: 300;
		text-transform: uppercase;
		white-space: nowrap;
		color: var(--color-menu);
	}
	
	.js .menu__item-textinner {
		transform: translateY(100%);
	}
	
	.menu__item:hover .menu__item-textinner {
		color: var(--color-menu-hover);
	}
	
	.menu__item-sub {
		display: none;
		text-transform: uppercase;
		font-weight: bold;
		white-space: nowrap;
		align-items: center;
		position: relative;
		margin-left: 2rem;
		padding-left: 3rem;
		opacity: 0;
		transform: translateX(-1rem);
		transition: transform 0.3s, opacity 0.3s;
	}
	
	.menu__item:hover .menu__item-sub {
		opacity: 1;
		transform: translateX(0);
	}
	
	.menu__item-sub::before {
		content: '';
		position: absolute;
		left: 0;
		top: 15%;
		width: 1px;
		height: 70%;
		background: currentColor;
		transform-origin: 0 100%;
		transform: rotate(22.5deg) scale3d(1,0,1);
		transition: transform 0.3s;
	}
	
	.menu__item:hover .menu__item-sub::before {
		transform: rotate(22.5deg) scale3d(1,1,1);
	}
	
	.hover-reveal {
		position: absolute;
		z-index: -1;
		width: 220px;
		height: 320px;
		top: 0;
		left: 0;
		pointer-events: none;
		opacity: 0;
	}
	
	.hover-reveal__inner {
		overflow: hidden;
	}
	
	.hover-reveal__inner,
	.hover-reveal__img {
		width: 100%;
		height: 100%;
		position: relative;
	}
	
	.hover-reveal__img {
		background-size: cover;
		background-position: 50% 50%;
	}
	
	.cursor {
		display: none;
	}
	
	.credits {
		align-self: flex-start;
		padding: 5rem 0 0 0;
	}
	
	.credits a {
		text-decoration: underline;
	}
	
	@media screen and (min-width: 53em) {
		.frame {
			flex-direction: column;
			align-items: flex-end;
			text-align: right;
			background: none;
			height: 50vh;
			pointer-events: none;
			padding-bottom: 0;
		}
		.frame__links a:not(:last-child) {
			margin-right: 0;
		}
		.frame__links {
			margin: 0.5rem 0 2rem;
			justify-self: start;
		}
		.frame__links a {
			margin-left: 0.5rem;
			pointer-events: auto;
		}
		.frame__button {
			cursor: not-allowed;
			pointer-events: auto;
			margin-top: auto;
			transform: translateY(50%);
		}
		.menu {
			padding-top: 20vh;
		}
		.menu__item {
			padding-left: 25vw;
			justify-content: initial;
		}
		.menu__item-text {
			padding: 1vh 0;
			font-size: 4vw;
		}
		.menu__item-sub {
			display: flex;
		}
	}
	
	@media (any-pointer: fine) {
		.cursor {
			position: fixed;
			top: 0;
			left: 0;
			display: block;
			pointer-events: none;
		}
		.cursor__inner {
			fill: var(--cursor-fill);
			stroke: var(--cursor-stroke);
			stroke-width: var(--cursor-stroke-width);
			opacity: 0.3;
		}
		.credits {
			padding-left: 25vw;
		}
	}
	